.container {
    max-width: 320px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
  
    @media screen and (min-width: 768px) {
      padding-left: 32px;
      padding-right: 32px;
      max-width: 768px;
    }
  
    @media screen and (min-width: 1280px) {
      padding-left: 16px;
      padding-right: 16px;
      max-width: 1280px;
    }
  }
  