h1, h2, h3, ul, p {
  margin: 0;
}

ul {
  padding: 0;
}

body {
  background-color: #bdbdbd;
  font-family: Montserrat, sans-serif;
  position: relative;
}

section {
  min-width: 100%;
}

img {
  width: 100%;
  max-width: 100%;
  display: block;
}

address {
  font-style: normal;
}

button {
  background: none;
  border: none;
  outline: none;
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

.container {
  max-width: 320px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.button {
  width: 145px;
  height: 40px;
  color: #fff;
  cursor: pointer;
  background: #f3848c;
  border: none;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.22;
  transition: background-color, all .25s, easy;
  display: flex;
  box-shadow: 10px 15px 25px rgba(232, 183, 183, .45);
}

.button:hover, .button:focus {
  color: #f3848c;
  background-color: #fff;
  border: 1px solid #f3848c;
}

.button:active {
  box-shadow: 10px 15px 25px rgba(232, 183, 183, .45), inset 0 4px 4px rgba(107, 66, 66, .25);
}

.header {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.header__container {
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 16px;
  padding-bottom: 16px;
  display: flex;
}

.header__icon-burger {
  cursor: pointer;
}

.modal-window {
  width: 100%;
  height: 100vh;
  background-color: #e5e2e3;
  transition: transform .25s cubic-bezier(.165, .84, .44, 1);
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-101%);
}

.modal-window__header {
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 16px;
  padding-bottom: 16px;
  display: flex;
}

.site-nav {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.site-nav__item {
  text-align: center;
}

.site-nav__item:not(:last-child) {
  margin-bottom: 40px;
}

.site-nav__link {
  width: 174px;
  cursor: pointer;
  letter-spacing: 0;
  color: #3c322d;
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 28px;
  font-weight: 500;
  line-height: 1.2;
  transition: color .25s cubic-bezier(.075, .82, .165, 1);
  display: block;
}

.site-nav__link:hover, .site-nav__link:focus {
  color: #fff;
  background: linear-gradient(rgba(248, 110, 103, .63) 0%, rgba(253, 86, 143, .86) 100%);
  box-shadow: 0 7px 27px -2px rgba(253, 88, 140, .35);
}

.modal-window__icon-close {
  cursor: pointer;
}

.is-open {
  transform: translateY(0);
}

.hero {
  height: 100vh;
  background-image: url("hero-background__mobile.bd153132.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  align-items: center;
  display: flex;
}

.hero__container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.hero__title {
  text-align: center;
  letter-spacing: -.03em;
  color: #3c322d;
  margin-bottom: 18px;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 28px;
  font-weight: 600;
  line-height: 1.2;
}

.hero__subtitle {
  color: #3c322d;
  margin-bottom: 28px;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 16px;
